object WorksStyles {
    val GridStyle = CssStyle {
        base {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(6) { size(2.fr) } }
                    columns { repeat(1) { size(2.fr) } }
                }
        }
        md {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(3) { size(2.fr) } }
                    columns { repeat(2) { size(2.fr) } }
                }
        }
        lg {
            Modifier
                .gap(48.px)
                .padding(48.px)
                .grid {
                    rows { repeat(2) { size(2.fr) } }
                    columns { repeat(3) { size(2.fr) } }
                }
        }
    }
}
